<script>
const echarts = require('echarts/lib/echarts');
require('echarts/lib/component/title');
require('echarts/lib/component/toolbox');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/grid');
require('echarts/lib/component/legend');
require('echarts/lib/chart/line');

export default {
  data(){
    return{
       radio1: {
        days:30,
       },
       repairs:{},
       xAxisdata:[],
       repairsdata:[],
       questionsdatadata:[],
       query:{
        days:30,
       }
    }
  },
  created(){
    this.getrepairs()
    this.getecharts()
  },
  mounted(){
    this.initcharts()
  },
  methods:{

    getchange(){
      this.query = this.radio1
      // console.log(this.query)
      this.getecharts()
    },



    // 缓冲
    async getecharts(){
        await this.repairsList()   //保修
        await this.questionsList()  //投诉
        this.initcharts()
    },

    //总数统计
    async getrepairs(){
      let res = await this.http.get('/repairs/getcounts')
      // console.log(res)
      if(res.errCode == 10000){
        this.repairs = res.data
      }
    },
    // 保修数据
    async repairsList(){
      // console.log(this.query)
      let res = await this.http.get('/repairs/getCountsList',this.query)
      // console.log(res)
      if(res.errCode == 10000){
       if(this.query.years){
          this.xAxisdata = res.data.map(imet=>imet.label)
          this.repairsdata = res.data.map(imet=>imet.counts)
       }else{
          this.xAxisdata = res.data.map(imet=>imet.label).join(',').split(',').reverse(',')
          this.repairsdata = res.data.map(imet=>imet.counts).join(',').split(',').reverse(',')
      }
       }
      // console.log(this.xAxisdata)
    }, 
    // 投诉数据
    async questionsList(){
      let res = await this.http.get('/questions/getCountsList',this.query)
      // console.log(res)
      if(res.errCode == 10000){
        if(this.query.years){
            this.questionsdata = res.data.map(imet=>imet.counts)
        }else{
            this.questionsdata = res.data.map(imet=>imet.counts).join(',').split(',').reverse(',')
        }
        
      }
      // console.log(this.countsdata)
    },
    // 折线图
    initcharts(){

        var myChart = echarts.init(this.$refs.pie);
        var option;

        option = {
          title: {
            text: '数据展示'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['保修', '投诉',]
          },
          grid: {
            left: '1%',
            right: '2%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.xAxisdata
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '保修',
              type: 'line',
              stack: 'Total',
              data: this.repairsdata
            },
            {
              name: '投诉',
              type: 'line',
              stack: 'Total',
              data: this.questionsdata
            },
          ]
        };

        option && myChart.setOption(option);

    }
  }
}
</script>

